///////////////////////////////////////////////////////////////////////////////
//
//  MAIN CONTENT
//
///////////////////////////////////////////////////////////////////////////////

.s-docs, .s-page, .s-program {
	@include block('main-content') {
		padding-top: $navbar-brand-height + ($navbar-padding-y*2);
		@include media-breakpoint-up('md') {
			padding-top: $navbar-height ;
		}
	}
}

.s-program {
	@include block('main-content') {
		@include media-breakpoint-down('sm') {
			height: 100%;
		}
	}
}