@include block('program') {
	padding-top: 3rem;
	padding-bottom: 3.5rem;
	border-bottom: 1px dotted $gray-300;
	@include media-breakpoint-up('md') {
		padding-top: 8rem;
		height: 96rem;
	}
	@include element('heading') {
		margin-top: .5rem;
		margin-bottom: 2rem;
	  @include block-text (24,24,16);
		@include media-breakpoint-up('md') {
			@include block-text (16,24,16);
			font-weight: 600;
			margin-bottom: 0;
			float: left;
		}
	}
	@include element('item') {
		@include media-breakpoint-up('md') {
			cursor: pointer;
			transition: background .25s ease-in-out; 
		}
		&:hover {
			.btn {
				@extend .btn-primary;
			}
		}
	}
	.nav-pills {
		margin-bottom: 1rem;
		@include media-breakpoint-down('sm') {
			justify-content: center;
		}
		@include media-breakpoint-up('md') {
			margin-bottom: 2rem;
		}
		.nav-link {
			font-size: 87.5%;
			font-weight: 600;
			border-radius: 100rem;
			background-color: $white;
			border: 2px solid rgba(62,60,92,0.16);
			color: $dark;
			@include media-breakpoint-up('md') {
				font-size: 1rem;
			}
		}
		.nav-link.active {
			border:	0;
			background-color: $primary;
			color: $white;
		}
	}
	.carousel-control-prev, .carousel-control-next {
		width: 2rem;
		height: 2rem;
	}
	.carousel-control-next-icon, .carousel-control-prev-icon {
	  background-size: .6rem;
	  background-position: center;
	  width: 2rem;
	  height: 2rem;
		border-radius: 100rem;
		border: 2px solid $gray-300;
	}
	tbody {
		@include media-breakpoint-down('sm') {
			display: block;
		}
	}
	@include element('head') {
		background-color: $white;
		th {
			border-top: 0;
			border-bottom: 0 !important;
		}
		@include media-breakpoint-down('sm') {
			display: none;
		}
	}
	@include element('td') {
		@include media-breakpoint-up('md') {
			border-top: 1px dotted $gray-300 !important;
		}
	}
	@include element('inner') {
		@include media-breakpoint-up('md') {
			font-size: 87.5%;
		}
	}
	@include element('schedule') {
		@include media-breakpoint-down('sm') {
			@include block-text (24,24,16);
			margin-bottom: 1rem;
			margin-top: .5rem;
		}
		@include media-breakpoint-up('md') {
			width: 20%;
		}
	}
	@include element('activity') {
		@include media-breakpoint-down('sm') {
			@include block-text (20,24,16);
			margin-bottom: .5rem;
		}
		@include media-breakpoint-up('md') {
			width: 40%;
		}
	}
	@include element('speaker') {
		@include media-breakpoint-down('sm') {	
			@include block-text (12,16,16);
			margin-bottom: 1rem;
			display: block;
			color: $red;
			font-weight: 600;
			text-transform: uppercase;
		}
		@include media-breakpoint-up('md') {
			width: 30%;
		}
	}
	@include element('details') {
		@include media-breakpoint-up('md') {
			width: 10%;
			padding-top: 0.75rem !important;
  		padding-bottom: 0 !important;
		}
	}
}