// Headings Size

h1, .h1 {
  @include block-text (32,24,16);
	@include media-breakpoint-up('md') {
		@include block-text (64,48,16);
	}
}

h2, .h2, {
  @include block-text (24,24,16);
	@include media-breakpoint-up('md') {
		@include block-text (48,48,16);
	}
}

h3, .h3 {
  @include block-text (16,24,16);
}

.text-big {
	@include block-text (16,24,16);
	@include media-breakpoint-up('sm') {
		@include block-text (24,36,16);
	}
}