// Carousel fade transition
.carousel-fade {
  .carousel-inner {
      height: 100%;
  }
  .carousel-item {
    display: block;
    position: absolute;
    opacity: 0;
    transition: opacity .75s ease-in-out;
    z-index: 0;
    &.active {
      opacity: 1;
      z-index: 1;
    }
  }
  .carousel-control-prev,
  .carousel-control-next {
    z-index: 2
  }

}


.carousel {
  .carousel-caption{
    top: 0;
    bottom: 0;
    margin: auto;
    left: 0;
    right: 0;
    width: 100%;
  }
}
