///////////////////////////////////////////////////////////////////////////////
//
//  NAV COMPONENT
//
///////////////////////////////////////////////////////////////////////////////

$navbar-offset-y: 4;
$navbar-height : $navbar-offset-y + $navbar-brand-height + ($navbar-padding-y);

@include block('main-navbar') {
	@extend .navbar;
	@include transition(background-color .25s ease-in-out, padding .25s ease-in-out, width 0s ease-in-out);
	transition-delay: .5s;

	@include media-breakpoint-up('md') {
		width: 83.3333333333%;
		padding-top: $navbar-offset-y + 0em;
		padding-left: 0;
		padding-right: 0;
		margin: auto;	
	}
	@include element('brand') {
		@extend .navbar-brand;
		@include transition(color .25s ease-in-out);
		transition-delay: .5s;
		background-image: url('#{$img}page/serious-monkey-logo-dark.svg');
		background-repeat: no-repeat;
		background-position: left 41%; 
		background-size: 5rem;
		text-indent: -999em;
		width: 5rem;
		@include media-breakpoint-up('md') {
			background-position: left 23%;
			background-size: 6.75rem;
			width: 6.75rem;
			background-image: url('#{$img}page/serious-monkey-logo.svg');
		}
	}
	.navbar-nav, .dropdown-item {
		font-size: 87.5%;
	}
	@include media-breakpoint-down('sm') {
		.navbar-nav, .dropdown-item {
			font-size: 1rem;
		}
		.navbar-nav {
		  margin-right: -$grid-gutter-width/2 !important;
		  margin-left: -$grid-gutter-width/2 !important;
		}
		.nav-link {
		  padding-right: $grid-gutter-width/2 !important;
		  padding-left: $grid-gutter-width/2 !important;
		  border-top: 1px solid $gray-800;

		}
		.navbar-nav .dropdown-menu {
			position: relative !important;
  		transform: translate3d(0px, 0px, 0px) !important;
  		padding: 0;
  		margin: 0;
		}
		.dropdown-item {
			padding: .5rem 1.611111111rem;
			border-top: 1px solid $gray-800;
		}
	}
	.nav-link {
		//font-size: 0.875rem;
		font-weight: 700;
		@include transition(color .25s ease-in-out);
	}
	.nav-item:last-child .nav-link {
		//padding-right: 0;
	}
	.navbar-toggler {
		// fixed fontsize for toggler button
		@include media-breakpoint-down('sm') {
		  float: right;
		  margin-top: 1.25rem;
		}
		font-size: 16px;
		border: 0;

	}
}
