

@mixin root-fontSize ($viewport-width) {
    @media ( min-width: $viewport-width / 16 * 1em ) {
        @if $viewport-width > 959  {
            html { font-size: ( $viewport-width ) / $ratio * 1 * 1.333333333 + 0%; }
        } @else if $viewport-width < 480 {
            html { font-size: ( $viewport-width ) / $ratio * 4 * 1.333333333 + 0%; }
        } @else {
            html { font-size: ( $viewport-width ) / $ratio * 2 * 1.333333333 + 0%; }
        }
    }
}

@mixin generate-fontsize($fontSize-from, $fontSize-to:"", $fontSize-every:"") {
    @if $fontSize-generate == true {


        @if $fontSize-to != null {
            $fontSize-to: $fontSize-to+1;
            @while $fontSize-from < $fontSize-to {
                @include root-fontSize ($fontSize-from);
                $fontSize-from: $fontSize-from + $fontSize-every;
            }
        } @else {
            @include root-fontSize ($fontSize-from);
        }

    }
}

/*
///////////////////////////////////////////////////////////////////////////////
//
//  FONT-SIZE
//
///////////////////////////////////////////////////////////////////////////////
*/

@each $label, $map in $generator-font-size {
  $from: map-get($map, from);
  $to: map-get($map, to);
  $step: map-get($map, step);

  @include generate-fontsize($from, $to, $step);

}