
// Class name
$fontFamily-placeholder                    : "." !default;
$fontFamily-class-name                     : "font-family" !default;
$fontFamily-class-separator                : "-" !default;
$fontFamily-column-separator               : "-" !default;
$fontFamily-of                             : "-" !default;

@each $fontFamily-keyName, $font-family, $alternative-font-a, $alternative-font-b, $alternative-font-c, $alternative-font-d, $alternative-font-e, $alternative-font-f in $font-families-class {
  #{$fontFamily-placeholder}#{$fontFamily-class-name}#{$fontFamily-column-separator}#{$fontFamily-keyName} {
    font-family: $font-family, $alternative-font-a, $alternative-font-b, $alternative-font-c, $alternative-font-d, $alternative-font-e, $alternative-font-f !important;
  }
}

