body.navbar-is-open {
  height: 100%;
}
.navbar-is-open.s-global {
  @include block('main-header') {
    height: 100%;
    @extend .bg-dark;
  }
  @include block('main-navbar') {
  	@extend .navbar-dark;
  	@extend .bg-dark;
    @include media-breakpoint-down('sm') {
      // this is set scrollable the menu on mobile device
      height: 100%;
      overflow: scroll;
    }
    @include element('brand') {
      background-image: url('#{$img}page/serious-monkey-logo-dark.svg');
    }
  }
}

.is-scrolled.s-global {
  @include block('main-header') {
    height: auto;
  }
	@include block('main-navbar') {
		@extend .navbar-dark;
		background-color: $black;
		width: 100%;
    padding-top: $navbar-padding-y;
    padding-left: $navbar-padding-x;
    padding-right: $navbar-padding-x;
    @include element('brand') {
      background-image: url('#{$img}page/serious-monkey-logo-dark.svg');
    }
    // @include media-breakpoint-down('sm') {
    //   background-color: transparent !important;
    //   @include element('brand') {
    //     opacity: 0;
    //   }
    //   .navbar-toggler {
    //     background-color: $black;
    //   }
    // }
    @include media-breakpoint-down('sm') {
      padding-top: 0;
      padding-bottom: 0;
    }
    @include media-breakpoint-up('md') {
      // this show and hide the navbar only on desktop version
      animation-name: navbar-transition-up;
      animation-duration: 1.5s;
    }
	}
}

.isnot-scrolled.s-global {
	@include block('main-navbar') {
    // this show and hide the navbar only on desktop version
    @include media-breakpoint-up('md') {
      animation-name: navbar-transition-down;
      animation-duration: 1.5s;
    }
	}
}
@include keyframes(navbar-transition-up) {
  0%   {opacity: 1;}
  25%  {opacity: 0;}
  50% {
  	opacity: 0;
  	transform: translateY(-$base-distance * 2);
  }
  100% {
		opacity: 1;
		transform: translateY(0);
  }
}

@include keyframes(navbar-transition-down) {
  0%   {opacity: 1;}
  25%  {opacity: 0;}
  50% {
  	opacity: 0;
  	transform: translateY(-$base-distance * 2);
  }
  100% {
		opacity: 1;
		transform: translateY(0);
  }
}