@include block('countdown') {

	@include element('container') {
		width: 16rem;
		display: block;
		margin: auto;
		@include media-breakpoint-up('md') {
			width: 32rem;
		}
	}
	@include element('time') {
		float: left;
		display: block;
		width: 4rem;
	  @include block-text (40,48,16);
		@include media-breakpoint-up('md') {
			width: 8rem;
			@include block-text (80,96,16);
		}
	}
	@include element('tag') {
		float: left;
		display: block;
		width: 4rem;
		@include block-text (10.666666,16,16);
		@include media-breakpoint-up('md') {
			width: 8rem;
			@include block-text (21.3333333,32,16);
		}
	}
	@include element('days') {

	}
	@include element('hours') {

	}
	@include element('min') {

	}
	@include element('sec') {

	}
}