@include block('splash-screen') {
	.modal-dialog {
	  max-width: 100%;
	  height: 100%;
	  margin: 0;
	  transition: initial !important;
  	transform: initial !important;
  	// eliminate the scrollbar
  	overflow-y: initial !important
	}
	.modal-content {
		height: 100%;
		background-color: $blue;
	  border: 0px;
	  border-radius: 0;
	}
	.modal-body {
		display: table;
	  height: 100%;
	  width: 100%;
	  padding: 0;
	}
	@include element('container') {
		display: table-cell;
  	vertical-align: middle;
	}
	@include element('content') {
  	width: 20rem;
		@include media-breakpoint-up('md') {
	  	width: 40rem;
	  	height: 12rem;
		}
	}
	@include element('logo') {
  	width: 20rem;
  	img {
  		width: 9rem;
  	}
		@include media-breakpoint-up('md') {
	  	img {
	  		width: 18rem;
	  		float: left;
	  	}
		}
	}
	@include element('claim') {
  	width: 20rem;
	  font-size: .875rem;
	  line-height: 2rem;
	  letter-spacing: .15rem;
		@include media-breakpoint-up('md') {
			font-size: 2.25rem;
			line-height: 2rem;
			margin-top: 3.25rem;
		  border-left: 3px solid #fff;
		  padding-left: 2rem;
		}
	}
}
