@include block('tickets') {
	padding-top: 2rem;
	padding-bottom: 2rem;
	@include element('partners-container') {

	}
	@include element('partner') {
		height: 4rem;
		background-size: 75% 75%;
		background-repeat: no-repeat;
		background-position: center;
		opacity: 0.75;
		transition: all 0.2s linear;
		&:hover {
			opacity: 1;
		}
		@include modifier('semrush') {
			background-image: url('#{$img}home/semrush-logo.svg');
		}
		@include modifier('darioflaccovio') {
			background-image: url('#{$img}home/darioflaccovio-editore-logo.svg');
		}
	}
	@include media-breakpoint-up('md') {
		padding-top: 8rem;
		padding-bottom: 8rem;
	}
	@include element('iframe') {
		height: 73rem;
		@media only screen and (min-width: 330px) {
			height: 70rem;
		}
		@media only screen and (min-width: 370px) {
			height: 68rem;
		}
		@media only screen and (min-width: 400px) {
			height: 67rem;
		}
		@media only screen and (min-width: 430px) {
			height: 64rem;
		}
		@media only screen and (min-width: 440px) {
			height: 63rem;
		}
		@media only screen and (min-width: 450px) {
			height: 62rem;
		}
		@media only screen and (min-width: 460px) {
			height: 61rem;
		}
		@media only screen and (min-width: 490px) {
			height: 60rem;
		}
		@media only screen and (min-width: 500px) {
			height: 56rem;
		}
		@media only screen and (min-width: 500px) {
			height: 56rem;
		}
		@media only screen and (min-width: 540px) {
			height: 60rem;
		}
		@media only screen and (min-width: 540px) {
			height: 60rem;
		}
		@media only screen and (min-width: 560px) {
			height: 59rem;
		}
		@media only screen and (min-width: 610px) {
			height: 58rem;
		}
