

@include block('illustration') {
	height: 15.5rem;
	margin-top: 3.5rem;
	background-repeat: no-repeat;
	background-position: center top;
	background-size: 14rem; 
	@include media-breakpoint-down('sm') {
		float: left;
	}
	@include media-breakpoint-up('md') {
		background-size: 40rem; 
		background-position: left top;
		height: 47.5rem;
		margin-top: -8rem;
	}
	@include modifier('right') {
		background-image: url('#{$img}home/bg-character-right.svg');
		@include media-breakpoint-up('md') {
			background-image: url('#{$img}home/bg-character-right-desktop.png');
		}
	}
	@include modifier('left') {
		background-image: url('#{$img}home/bg-character-left.svg');
		@include media-breakpoint-up('md') {
			background-image: url('#{$img}home/bg-character-left-desktop.png');
		}
	}
	@include modifier('party') {
		background-image: url('#{$img}home/bg-character-party.svg');
		@include media-breakpoint-up('md') {
			background-image: url('#{$img}home/bg-character-party-desktop.png');
		}
	}
	@include element('characters') {
		margin-top: 4rem;
		height: 12rem;
		img { width: 20rem; }
		@include media-breakpoint-up('md') {
			margin-top: 21.5rem;
			height: 24rem;
			img { width: 40rem; }
		}
	}
}