.s-program {
  @include block('main-navbar') {
    @extend .navbar-dark;
    @include element('brand') {
      background-image: url('#{$img}page/serious-monkey-logo-dark.svg');
    }
  }
	.main-header {
		@extend .bg-info;
	}
}

.s-home {
  @include block('main-navbar') {
    @extend .navbar-dark;
    @include element('brand') {
      background-image: url('#{$img}page/serious-monkey-logo-dark.svg');
      @include media-breakpoint-up('md') {
        background-image: url('#{$img}page/serious-monkey-logo.svg');
      }
    }
  }
}
