@include block('mailing-list') {
	background-image: url('#{$img}home/bg-mailing-list.svg');
	background-position: center top;
	background-size: 40rem 40rem;
	padding-top: 10rem;
	@include media-breakpoint-up('xs') {
		padding-top: 12rem;
	}
	@include media-breakpoint-up('sm') {
		background-size: 40rem*2 40rem*2;
	}
	@include media-breakpoint-up('md') {
		padding-top: 20rem;
		height: 40rem;
	}
	@include element('signup') {
		background-color: #2F3242;
	}
	@include element('heading') {
	  @include block-text (16,16,16);
		@include media-breakpoint-up('sm') {
			@include block-text (24,24,16);
			margin-bottom: 2.5rem;
		}
		@include media-breakpoint-up('md') {
			@include block-text (32,32,16);
			margin-bottom: 2.5rem;
			padding-left: 3rem;
			padding-right: 3rem;
		}
	}
	.form-inline .form-control {
		width: 100%;
	}
	.form-group {
		width: 100%;
		margin-bottom: 1rem;
		@include media-breakpoint-up('md') {
			width: 25%;
			padding-right: 1rem;
			height: 2.75rem;
		}
	}
	#mc-embedded-subscribe {
		width: 100%;
		margin-bottom: 1rem;
		@include media-breakpoint-up('md') {
			width: 25%;
		}
	}
	.form-control:focus {
	  box-shadow: 0 0 0 0.2rem rgba(241,95,85,1);
	}
	.form-check {
		width: 100%;
	}
	#mc_embed_signup div.mce_inline_error {
	  margin: .5rem auto .5em;
	  padding: 5px 1rem;
	  background-color: $danger;
	  font-weight: bold;
	  z-index: 1;
	  color: #fff;
	  font-size: .875rem;
	  border-radius: 1rem;
	}
}