
@include block('hero') {
	@include element('illustration') {
		height: 19rem;
		background-image: url('#{$img}home/sky-gradient-bg.png');
		background-size: 100% 19rem;
  	background-repeat: repeat-x;
		@include media-breakpoint-up(md) {
			height: 42.5rem;
			background-size: 100% (19rem*2);
      position: absolute;
		}
	}
  @include element('dates') {
    font-weight: 700;
    letter-spacing: .25rem;
    @include block-text (12,16,16);
    @include media-breakpoint-up('md') {
      @include block-text (16,24,16);
    }
  }
	@include element('intro') {
		@include media-breakpoint-up('md') {
      background: -moz-linear-gradient(left, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 32%, rgba(255,255,255,0) 54%, rgba(255,255,255,0) 100%); /* FF3.6-15 */
      background: -webkit-linear-gradient(left, rgba(255,255,255,1) 0%,rgba(255,255,255,1) 32%,rgba(255,255,255,0) 54%,rgba(255,255,255,0) 100%); /* Chrome10-25,Safari5.1-6 */
      background: linear-gradient(to right, rgba(255,255,255,1) 0%,rgba(255,255,255,1) 32%,rgba(255,255,255,0) 54%,rgba(255,255,255,0) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#00ffffff',GradientType=1 ); /* IE6-9 */
			position: absolute;
			margin-top: 17rem;
		}
	}
	@include element('mask') {
		background-image: url('#{$img}home/sky-mask.svg');
		background-size: 40rem 19rem;
		background-position: center top;
		@include media-breakpoint-up('md') {
			background-image: url('#{$img}home/sky-mask-desktop.svg');
			background-size: 80rem 38rem;
      background-position: left top;
		}
	}
	@include element('ship-container') {
		background-image: url('#{$img}home/ship-shadow.svg');
		background-position: center bottom;
		background-repeat: no-repeat;
		background-size: 10.71875rem .25rem;
		overflow: hidden;
		height: 12.2rem;
		width: 12rem;
		top:5rem;
		left: 0;
		right: 0;
    margin: auto;
		@include media-breakpoint-up('md') {
      width: 24rem;
      height: 24.4rem;
      top: 11rem;
      margin-right: 8rem;
      background-size: 10.71875rem*2 .25rem*2;
		}
	}
	@include element('anim-updown') {
		@include animation (updown 6s ease-in-out forwards infinite );
	}
	@include element('ship') {
		@include animation (sway 6s ease-in-out 1.5s forwards infinite );
	}
  @include element('bg-anim') {
    height: 9rem;
    top: 6rem;
    overflow: hidden;
    @include media-breakpoint-up(md) {
      height: 14rem;
      top: 19rem;
      right: 0;
    }
  }
	@include element('waves') {
		@include animation (wave 6s linear forwards infinite );
		background-image: url('#{$img}home/waves.svg');
	  background-repeat: repeat-x;
	  background-size: 12.5rem .5rem;
	  bottom: 0;
	  height: 1rem;
	  width: 12.5rem;
    @include media-breakpoint-up('md') {
      background-size: 25rem 1rem;
      width: 25rem;
      height: 2rem;
    }
    @include modifier('one') {

    }
    @include modifier('two') {
      // animation-duration: 4.5s;
      @include css3-prefix(animation-duration, 4.5s);
      @include opacity($opacity: 0.5);
    }
    @include modifier('three') {
      // animation-duration: 3s;
      @include css3-prefix(animation-duration, 3s);
      @include opacity($opacity: 0.5);
    }
	}
}



@include keyframes('wave') {
  0% { background-position: 0 bottom;}
  100% { background-position: 12.5rem bottom; }
}

@include media-breakpoint-up('md') {
  @include keyframes('wave') {
    0% { background-position: 0 bottom;}
    100% { background-position: 25rem bottom; }
  }
}

@include keyframes('sway') {
  0% {
    @include transform(rotate(6deg));
  }
  50% {
    @include transform(rotate(-6deg));
  }
  100% {
    @include transform(rotate(6deg));
  }
}

@include keyframes('updown') {
  0% { 
    @include transform(translateY(.5rem));
    @include transform(translateX(.25rem));
  }

  50% {
    @include transform(translateY(-.5rem));
    @include transform(translateX(-.25rem));
  }

  100% {
    @include transform(translateY(.5rem));
    @include transform(translateX(.25rem));
  }
}




// n is number of stars required
@function multiple-box-shadow($n, $color) {
  $value: "#{random(80)}rem #{random(80)}rem #{$color}";

  @for $i from 2 through $n {
    $value: "#{$value} , #{random(80)}rem #{random(80)}rem #{$color}";
  }

  @return unquote($value);
}

$shadows-small: multiple-box-shadow(75, #F3F9FC);
$shadows-medium: multiple-box-shadow(50, #F3F9FC);
$shadows-big: multiple-box-shadow(25, #F3F9FC);

$cloud-width: 2;
$cloud-height: .5;

$cloud-one: 1rem;
$cloud-two: 2rem;
$cloud-three: 4rem;


#clouds-sm {
  width: $cloud-width*$cloud-one;
  height: $cloud-height*$cloud-one;
  background: transparent;
  //box-shadow: $shadows-small;
  @include css3-prefix(box-shadow, $shadows-small);
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  border-top-left-radius: 50% 100%;
  border-top-right-radius: 50% 100%;
  //animation: clouds-anim 150s linear infinite;
  @include animation (clouds-anim 150s linear infinite);

  &:after {
    content: " ";
    position: absolute;
    top: 20rem;
	  width: $cloud-width*$cloud-one;
	  height: $cloud-height*$cloud-one;
    background: transparent;
    //box-shadow: $shadows-small;
    @include css3-prefix(box-shadow, $shadows-small);
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    border-top-left-radius: 50% 100%;
    border-top-right-radius: 50% 100%;
  }
}

#clouds-md {
  width: $cloud-width*$cloud-two;
  height: $cloud-height*$cloud-two;
  background: transparent;
  // box-shadow: $shadows-medium;
  @include css3-prefix(box-shadow, $shadows-medium);
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  border-top-left-radius: 50% 100%;
  border-top-right-radius: 50% 100%;
  //animation: clouds-anim 100s linear infinite;
  @include animation (clouds-anim 100s linear infinite);

  &:after {
    content: " ";
    position: absolute;
    top: 20rem;
	  width: $cloud-width*$cloud-two;
	  height: $cloud-height*$cloud-two;
    background: transparent;
    // box-shadow: $shadows-medium;
    @include css3-prefix(box-shadow, $shadows-medium);
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    border-top-left-radius: 50% 100%;
    border-top-right-radius: 50% 100%;
  }
}

#clouds-lg {
  width: $cloud-width*$cloud-three;
  height: $cloud-height*$cloud-three;
  background: transparent;
  // box-shadow: $shadows-big;
  @include css3-prefix(box-shadow, $shadows-big);
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  border-top-left-radius: 50% 100%;
  border-top-right-radius: 50% 100%;
  //animation: clouds-anim 50s linear infinite;
  @include animation (clouds-anim 50s linear infinite);

  &:after {
    content: " ";
    position: absolute;
    top: 20rem;
	  width: $cloud-width*$cloud-three;
	  height: $cloud-height*$cloud-three;
    background: transparent;
    // box-shadow: $shadows-big;
    @include css3-prefix(box-shadow, $shadows-big);
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
    border-top-left-radius: 50% 100%;
    border-top-right-radius: 50% 100%;
  }
}

@keyframes clouds-anim {
  0% {
    @include opacity($opacity: 0);
    //transform: translateX(-80rem);
    @include transform(translateX(-80rem));
  }
  1% {
    @include opacity($opacity: 1);
  }
  100% {
    //transform: translateX(80rem);
    @include transform(translateX(80rem));
  }
}




// n is number of stars required
@function multiple-box-shadow($n) {
  $value: "#{random(2000)}px #{random(2000)}px #FFF";

  @for $i from 2 through $n {
    $value: "#{$value} , #{random(2000)}px #{random(2000)}px #FFF";
  }

  @return unquote($value);
}

$shadows-small: multiple-box-shadow(1000);
$shadows-medium: multiple-box-shadow(500);
$shadows-big: multiple-box-shadow(250);

#sea-sm {
  width: 1px;
  height: 1px;
  background: transparent;
  box-shadow: $shadows-small;
  //animation: animStar 200s linear infinite;
  @include animation (animStar 200s linear infinite);

  &:after {
    content: " ";
    position: absolute;
    top: 2000px;
    width: 1px;
    height: 1px;
    background: transparent;
    box-shadow: $shadows-small;
  }
}

#sea-md {
  width: 2px;
  height: 2px;
  background: transparent;
  box-shadow: $shadows-medium;
  //animation: animStar 150s linear infinite;
  @include animation (animStar 150s linear infinite);

  &:after {
    content: " ";
    position: absolute;
    top: 2000px;
    width: 2px;
    height: 2px;
    background: transparent;
    box-shadow: $shadows-medium;
  }
}

#sea-lg {
  width: 3px;
  height: 3px;
  background: transparent;
  box-shadow: $shadows-big;
  //animation: animStar 125s linear infinite;
  @include animation (animStar 125s linear infinite);

  &:after {
    content: " ";
    position: absolute;
    top: 2000px;
    width: 3px;
    height: 3px;
    background: transparent;
    box-shadow: $shadows-big;
  }
}



@keyframes animStar {
  from {
    //transform: translateX(-62rem);
    @include transform(translateX(-62rem));
  }

  to {
    //transform: translateX(62rem);
    @include transform(translateX(62rem));
  }
}
